<template>
	<div class="page1_4">
		<div class="nav">
			<ul class="fl">
				<li>
					<router-link active-class="active" class="item" :to="{path:'/inside/page1_4'}">
						审计类案推荐
					</router-link>
				</li>
			</ul>
			<!-- <div class="fr inter">
				<interBtn title="交互调整" id="more4_4"/>
			</div> -->
		</div>
		<div class="content_page">
			<div class="page-top" style="clear: both;height: 50px;">
				<Place class="fl" :data="msg"/>
				<div class="fr inter">
					<interBtn title1="审计案例及经验" title="审计经验" id="more1_4" ydname="审计准备"/>
				</div>
			</div>
			<el-dialog :visible.sync="dialogVisible" width="50%" class="xqModel">
				<div class="flxq">
				    <p class="cont" v-html="cont"></p>
				</div>
			</el-dialog>
			<div class="cont box-border-shadow">
				<div class="find">
					<div style="margin-top: 15px;width: 40%;margin: 0 auto">
					  <el-input placeholder="请输入内容" v-model="findWord" class="input-with-select">
					    <el-button slot="append" icon="el-icon-search" @click="getData(findWord)"></el-button>
					  </el-input>
					  <div class="word_main">
					  	<ul>
					  		<li>推荐:</li>
					  		<li v-for="item in keys" class="item" @click="getData(item)">{{item}}</li>
					  		<!-- <li class="item">南京审计大学</li>
					  		<li class="item" @click="getData(this)">教育部</li>
					  		<li class="item" @click="getData(this)">商学院</li>
					  		<li class="item" @click="getData(this)">信息工程学院</li>
					  		<li class="item" @click="getData(this)">审计署</li> -->
					  	</ul>
					  </div>
					</div>
	            </div>
	            <div class="box-border-shadow">
	            	<el-row :gutter="20">
					  <el-col :span="18">
					  	<div class="grid-content bg-purple">
					  		<div id="graphCharts" style="width: 100%;height: 800px;"></div>
						</div>
					  </el-col>
					  <el-col :span="6" style="border-left: 1px solid #ccc">
					  	<div class="grid-content bg-purple">
					  		<div style="height: 800px;overflow-y:auto; ">
					  			<div style="text-align: left;font-size: 16px;margin: 30px 0;color: #b80000">相关推荐：</div>
					  			<div style="text-align: left;">主体:</div>
					  			<ul class="keys key1">
					  				<li v-for="item in keys_zhuti" @click="getData(item)">{{item}}</li>
					  			</ul>
					  			<div style="text-align: left;">值:</div>
					  			<ul class="keys key2">
					  				<li v-for="item in keys_zhi" @click="getData(item)">{{item}}</li>
					  			</ul>
					  		</div>
					  	</div>
					  </el-col>
					</el-row>
	            </div>
				<ul class="leianList">
	                <li class="item" v-for="item in dataList">
	                    <p class="item-head"><span class="icon-fl">类案推荐</span> <span class="title" @click="xqModel(item)">{{item.titl}}</span></p>
	                    <p class="item-b"><span class="btn1 btn">来源：{{item.laiyuan}}</span> &nbsp;&nbsp;|&nbsp;&nbsp; <span class="btn2 btn">所属领域：{{item.leibie}}</span> &nbsp;&nbsp;|&nbsp;&nbsp; <span class="btn3 btn">时间：{{item.times}}</span></p>
	                </li>
	            </ul>
			</div>
		</div>
	</div>
</template>
<script type="text/javascript">
	import interBtn from './interactions'
	import Place from './page/place'
	import * as echarts from 'echarts'
	import reqData from "./../../reqData.js"
	export default{
		name:'page1_4',
		components:{
			interBtn,
			Place
		},
		data(){
			return{
				msg:["审计准备","审计类案推荐"],
				dialogVisible:false,
				cont:'',
				dataList:[],
				findWord:'',
				keys:['南京审计大学','教育部','商学院','信息工程学院','审计署','公共管理学院'],
				keys_zhuti:['南京审计大学','教育部','商学院','信息工程学院','审计署','公共管理学院'],
				keys_zhi:['一百万元及以上的','单笔金额5万元及以上100万元以下的']
			}
		},
		computed:{
			companyID(){
				return JSON.parse(sessionStorage.getItem("company")).id;
			},
		},
		methods: {
	      xqModel(datas){
		      	this.dialogVisible=true;
		      	let cont=datas.text;
		      	let title=datas.titl;
		      	let laiyuan=datas.laiyuan;
		      	let leibie=datas.leibie;
		      	let time=datas.times;
		      	let reg=new RegExp(" ","g");
				cont=cont.replace(reg,"</p><p>");
				let titlHtml="<p style='text-align:center;color:#b80000;font-size:22px;margin-bottom:6px;'>"+title+"</p>";
				let desc=`<p style='text-align:center;color:#999;font-size:13px;'><span>来源：`+laiyuan+`</span> &nbsp;&nbsp;|&nbsp;&nbsp; <span>所属领域：`+leibie+`</span> &nbsp;&nbsp;|&nbsp;&nbsp; <span>时间：`+time+`</span></p>`
				let contHtml="<p>"+cont+"</p>";
				
				this.cont=titlHtml+desc+contHtml;
	      },
	      getDataList(){
		      	reqData
			        .req({
			          apiName: "anlituijian",
			          data: {             
			            ID:this.companyID
			          }
			        })
			        .then(res => {
			          res = res.data;
			          if (res && res.code === "200") {
			            let data = res.data;
			            console.log(data);
			            this.dataList=data;
			          }
			        })
			        .catch(err => {
			          console.log(err);
			       });
	      },
	      unique(arr,links){
	   　　　var newArr = [arr[0].name];
	   		 var data=[arr[0]];
	   		 //去重复项
	  　　 	 for(var i=1;i<arr.length;i++){
	　　　　		if(newArr.indexOf(arr[i].name) == -1){
	          　　  newArr.push(arr[i].name);
	          	    data.push(arr[i]);
	    　　  	}
    		 }
    		 //区分值和主题
    		 for(var i=0;i<data.length;i++){
    		 	if(data[i].tag&&data[i].tag==='值'){
    		 		data[i].itemStyle={
			             normal: {
			                 color: '#68bdf6',
			                 borderColor:'#5ca8db',
			                 borderWidth:2,
			             }
			         }
			         data[i].symbol='diamond'
    		 	}
    		 }
    		 console.log(data);
	         return data;
		  },
		  getData(word){
			  	let _this=this;
			  	_this.keys_zhuti=[];
			  	_this.keys_zhi=[];
			 	 console.log(word);
			 	 word=word?word:'空';
			 	 var graphCharts = echarts.init(document.getElementById('graphCharts'));
			 	 graphCharts.showLoading();
			   	 reqData
			        .req({
			          apiName: "Neo4jToJson",
			           data: {             
			             word:'南京审计大学',
			             word1:'主体'
			           }
			        })
			        .then(res => {
			          res = res.data;
			          if (res && res.code === "200") {
			            let data = res.data;
			            console.log(data);
			            let nodes=data.nodes
						let links=data.links;
						console.log(nodes);
						nodes=this.unique(nodes);
						for (var i = 0; i < nodes.length; i++) {
							if(nodes[i].tag=="主体"){
								_this.keys_zhuti.push(nodes[i].name)
							}else{
								_this.keys_zhi.push(nodes[i].name)
							}
							
						}
						// links=this.linksFormat(links);
						graphCharts.hideLoading();
				    	graphCharts.setOption(this.graphOption(nodes,links)); 
				    	graphCharts.on('click', function (params) {
				    		console.log(params);
				    		if(params.dataType=="node"){
				    			reqData
						        .req({
						          apiName: "Neo4jToJson",
						           data: {             
						             word:params.data.name,
						             word1:'主体'
						           }
						        })
						        .then(res => {
						          res = res.data;
						          if (res && res.code === "200") {
						            let data = res.data;
						            console.log(data);
						            let nodes2=data.nodes
									let links2=data.links;
									nodes2=nodes.concat(nodes2);
									links2=links.concat(links2);
									console.log(nodes2);
									nodes2=_this.unique(nodes2);
									graphCharts.setOption({
								        series: [{
								            nodes: nodes2, 
											links:links2
								        }]
								    });

						          }
						        })
						        .catch(err => {
						          console.log(err);
						       });
				    		}
				    	});
			    	    $(window).resize(function(){
							graphCharts.resize({
								width:'auto',
								height:'auto'
							})
					    })

			          }
			        })
			        .catch(err => {
			          console.log(err);
			       });
		  },
		  graphOption(nodes,links){
		    	let option = {
				     title: {
				         text: ''
				     },
				     tooltip: {
				         formatter: function(x) {
				             return x.data.name;
				         }
				     },
				     series: [{
				         type: 'graph',
				         layout: 'force',
				         focusNodeAdjacency: true,
				         symbolSize: 48,
				         roam: true,
				         edgeSymbol: ['', 'arrow'],
				         // edgeSymbolSize: [80, 10],
				         hoverAnimation:true,
				         force: {
				         	initLayout:'circular',
				             repulsion: 600,
            				 layoutAnimation:false,
            				 gravity:0.5,
				             edgeLength: [16, 30],
				             layoutAnimation:true
				         },
				         draggable: true,
						 // symbol:'diamond',
				         itemStyle: {
				             normal: {
				                 color: '#ff756e',
				                 borderColor:'#e06760',
				                 borderWidth:2,
				             },

				         },
				         lineStyle: {
				             normal: {
				                 width: 1,
				                 color: '#000'

				             }
				         },
				         edgeLabel: {
				             normal: {
				                 show: true,
				                 textStyle: {fontSize: 14,color:'#000'},
				                 formatter: function(x) {
				                 	let name=x.data.name;
						         	if(name.length>5){
										name=name.substr(0,5)+'...';
										// v.nameSub=name;
									}else{
										name=name.substr(0,5);
										// v.nameSub=name;
									}
				                     return name;
				                 }
				             }
				         },
				         label: {
				         	show:true,
				            formatter: function(x) {
			                 	let name=x.data.name;
					         	if(name.length>3){
									name=name.substr(0,3)+'...';
									// v.nameSub=name;
								}else{
									name=name.substr(0,3);
									// v.nameSub=name;
								}
			                     return '{a|'+name+'}';
			                },
			                rich: {
						        a: {
						            color: '#fff',
						        },
						    }
				         },
				        nodes: nodes, 
						links:links 
				    }]
				};
				return option;
		  }
	    },
	    mounted(){
	    	this.getData('空');
	    	this.getDataList;
	    }
	}
</script>s
<style type="text/css" scoped="">
	.cont{
		text-align: center; 
		/*margin: 20px auto;*/
		min-height: 600px;
	    padding: 10px 40px;
	    border-radius: 8px;
	    background-color: #fff;
	}
	.cont .leianList>.item{
	    padding: 30px 0;
	    border-bottom: 1px solid #e0e0e0;
	}
	.cont .leianList>.item .btn{
		cursor: pointer;
	}
	.cont .leianList>.item .btn:hover{
		color: #b80000;
	}
	.cont .item p{
	    height: 28px;
	    line-height: 28px;
	    color: #999999;
	}
	.cont .leianList>.item .item-head{
	    margin-bottom: 14px;
	    cursor: pointer;
	    white-space: nowrap;
	    overflow: hidden;
	    text-overflow: ellipsis;
	}
	.cont .leianList>.item .item-head .icon-fl{
	    padding: 7px 12px;
	    background-color: #f59536;
	    border-radius: 5px;
	    color: #fff;
	}
	.cont .leianList>.item .item-head .title{
	    color: #333;
	    font-size: 18px;
	    /*font-weight: bold;*/
	}
	.cont .leianList>.item .item-head .title:hover{
		color: #b80000;
	}
	.cont .leianList>.item .item-b .key{
	    color: #128bed;
	}
	.cont .block{
		text-align: center;
		margin-top: 30px;
		margin-bottom: 80px;
	}
	/*弹出框法律详情样式结束*/
    .flxq .title{
	    color: #000;
	    font-size: 28px;
	    line-height: 60px;
	    text-align: center;
	}
	.flxq .decs{
	    color: #999999;
	    font-size: 14px;
	    line-height: 26px;
	    margin-bottom: 26px;
	    text-align: center;
	}
	.flxq .decs .key,
	.xwxq .decs .key{
	    font-size: 15px;
	    color: #00a2c9;
	}
	.flxq .cont{
	    color: #333;
	    font-size: 16px;
	    text-align:justify;
	    text-indent: 40px;
	    line-height: 34px;
	}
	.flxq .cont p{
	    margin-bottom: 16px;
	}
	/*弹出框法律详情样式结束*/
	.word_main{
		margin: 20 auto;
		overflow: hidden;
	}
	.word_main li{
		float: left;
		line-height: 40px;
		color: #a2a2a2;
	}
	.word_main li.item{
		margin: 0 8px;
		cursor: pointer;
	}
	.word_main li.item:hover{
		color: #b80000;
	}
	.keys{
		overflow: hidden;
	}
	.keys>li{
		float: left;
		display: block;
		background-color: #ccc;
		/*border: 1px solid #30333a;*/
		font-size: 15px;
		color: #30333a;
		padding: 4px 8px;
		border-radius: 5px;
		margin: 10px 5px;
		cursor: pointer;
	}
	.key2>li{
		background-color: #30333a;
		/*border: 1px solid #9195a0;*/
		color: #ccc;
	}
</style>


